<template>
  <div>
    <!-- path -->
    <h3 class="sub-list-title">仓库路径：</h3>
    <div class="text-path">{{ thePath }}</div>

    <!-- projects -->
    <h3 class="sub-list-title" v-show="projects.length > 0">项目：</h3>
    <div class="projects sub-list">
      <div
        class="projects-item sub-list-item"
        v-for="item in projects"
        :key="item.name"
        :title="item.path"
        @click="handleClickProjectItem(item)"
      >
        {{ item.name }}
      </div>
    </div>

    <!-- worktrees -->
    <h3 class="sub-list-title" v-show="worktrees.length > 0">工作树：</h3>
    <div class="worktrees sub-list">
      <div
        class="worktrees-item sub-list-item"
        v-for="item in worktrees"
        :key="item.name"
        :title="item.path"
        @click="handleClickWorktreeItem(item)"
      >
        {{ formatWorktreeName(item) }}
      </div>
    </div>

    <!-- submodules -->
    <h3 class="sub-list-title" v-show="submodules.length > 0">子模块：</h3>
    <div class="submodules sub-list">
      <div
        class="submodules-item sub-list-item"
        v-for="item in submodules"
        :key="item.name"
        :title="item.path"
        @click="handleClickSubmoduleItem(item)"
      >
        {{ item.name }}
      </div>
    </div>
  </div>
</template>

<script>
import GetterJS from "@/js/getter";

export default {
  name: "home-local-repository-detail",

  computed: {
    theIcon() {
      return this.GetByPath("icon", "/bad/icon/path");
    },

    theTitle() {
      return this.GetByPath("name", "");
    },

    theDescription() {
      return this.GetByPath("description", "");
    },

    thePath() {
      return this.GetByPath("path", "");
    },

    projects() {
      return this.GetByPath("projects", []);
    },

    submodules() {
      return this.GetByPath("submodules", []);
    },

    worktrees() {
      return this.GetByPath("worktrees", []);
    },
  },

  methods: {
    GetByPath(path, def) {
      return GetterJS.Get(this.data, path, def);
    },

    formatWorktreeName(item) {
      let name = item.name;
      if (name == "") {
        name = "[默认]";
      }
      return name;
    },

    handleClickProjectItem(item) {
      let intent = { project: item };
      let vo = { intents: [intent] };
      this.$store.dispatch("intent/start", vo);
    },

    handleClickSubmoduleItem(item) {
      let intent = { submodule: item };
      let vo = { intents: [intent] };
      this.$store.dispatch("intent/start", vo);
    },

    handleClickWorktreeItem(item) {
      let intent = { worktree: item };
      let vo = { intents: [intent] };
      this.$store.dispatch("intent/start", vo);
    },
  },

  props: {
    data: Object,
  },
};
</script>

<style scoped>
.text-path {
  font-size: 13px;
  color: gray;
}

.sub-list {
  display: flex;
  flex-direction: row;
  font-size: 13px;
}

.sub-list-title {
  margin-top: 10px;
  margin-left: 0px;
  margin-right: 0px;
  margin-bottom: 0px;
  padding: 0px;
  font-size: 16px;
  font-weight: 400;
}

.sub-list-item {
  padding: 10px;
  margin: 10px;
  border-style: solid;
  border-color: rgb(194, 194, 194);
  border-width: 1px;
  border-radius: 2px;
  cursor: pointer;
}

.sub-list-item:hover {
  border-color: gray;
  background-color: aquamarine;
}
</style>
